import styles from "./RightSidebar.module.css";
import LayerList from "../component/submenu/LayerList.js"
import { PlusOutlined}from "@ant-design/icons";
import { useState } from "react";
import Modal from "../component/Modal.js"


export default function RightSidebar (props) { 

  const [isDragging,setIsDragging]=useState(false);
  const [isDragOver,setIsDragOver]=useState(false);

  const handleDragStart=(e)=>{
    setIsDragging(true); //  drag?
    e.dataTransfer.effectedAllowed = "move"; //      
    e.dataTransfer.setData("targetId",e.target.id); // item data .[key],[value]
  }
  const handleDragOver = (e) => {
    e.preventDefault(); // touch     .


    console.log(e.target.innerText.substr(5,));

  };
  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragging(false);

    let newId = e.dataTransfer.getData('targetId'); // Start   
    // console.log(e.target.innerText);//FIXME:ID innerText .
    let oriId=e.target.innerText.substr(5,)
    // item   
    props.moveItem(props.Items,newId,oriId);
  };

  const dragOverItem=()=>{

  }
  
  const [isModalOpen,setIsModalOpen]=useState(false);
  const openModal= ()=>{
      setIsModalOpen(true);
  }
  const closeModal= ()=>{
      setIsModalOpen(false);
  }
  

  return (
    <>
    <div id ="rightsidebar" className={styles.container}>
      <div className={styles.itemList}>
      {/* props.addLayerItem */}
        <Modal isModalOpen={isModalOpen} closeModal={closeModal} />
        <div className={styles.addItem} onClick={openModal}>
          <PlusOutlined style={{fontSize:"20pt", color:"gray"}} />
        </div>
        <div className={styles.itemScroll}>
          <LayerList 
              Items={props.Items} 
              delItem={props.delItem} 
              handleDragStart={handleDragStart}
              handleDragOver={handleDragOver}
              isDragOver={isDragOver}
              handleDrop={handleDrop}
              canvas={props.canvas}/>
        </div>
      </div>
    </div>
    </>
  );
};
